.video-item {
    width:225px;
    border-radius:20px;
    text-align:left;
    margin-left:20px!important;
    margin-right: 20px!important;
    color: white;
    display: inline-block;
    position: relative;
}
.video-frame {
    width:100%;
    height:150px;
    background-color:black;
}
.category {
    font-size:25px;
    text-align: center!important;
}
.suggested-label {
    margin-bottom:10px;
    margin-top: 40px;
    font-size:22px;
}
.sub-details {
    position:relative;
    font-weight:bold;
    margin-top:5px;
    text-align:left;
}
/* .sub-details {
    width:250px;
    margin-top:2px;
} */
/*  */
.video-title {
    margin-top:2px;
    margin-bottom:0;
    font-size:13px;
    font-weight: bold;
}
.thumbnail {
    width: 320px;
    margin-bottom: 0;
    width: 200px!important;
    
}
.creator-name {
    position:relative;
    left:0;
}
.description-text {
    margin-bottom:0;
    margin-top:5px;
}
/* desktop styles */

@media only screen and (min-width: 700px) {
    .video-row ul {
        padding:0;
        display:table-row;
    }
    .video-row {
        width: 100%;
        min-height: 200px;
        margin-top: 5px;
        margin-bottom: 50px;
        
    }
    .video-item {
        width:225px;
        border-radius:20px;
        text-align:left;
        margin-left:10px!important;
        margin-right: 10px;
        color: white;
        display: inline-block;
        position: relative;
    }
    .video-row ul li {
        /* margin-right:30px; */
        /* width:22%; */
        display: table-cell;
        flex-direction: row;
        vertical-align: top;
    }
}
/* mobile styles */
@media only screen and (max-width: 700px) {
    .video-row ul {
        padding:0;
        /* display:table-row; */
    }
    .video-row {
        width: 100%;
        min-height: 200px;
        margin-top: 5px;
        margin-bottom: 50px;
    }
    .video-item {
        width:275px!important;
        border-radius:20px;
        text-align:left;
        color: white;
        display: block!important;
        position: relative;
        margin-left:0!important;
    }
    .video-row ul li {
      
        /* display: table-cell; */
        flex-direction: row;
        vertical-align: top;
        width: 275px!important;
        margin-left:0!important;
    }
  }