input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
 
  -webkit-text-fill-color: white;
  -webkit-box-shadow: 0 0 0px 1000px #181818 inset;
  transition: background-color 5000s ease-in-out 0s;
}


.login-btn {
    width: 100%;
    margin-top:10px;
    border-radius: 10px;
    font-weight: bold;
    padding:5px;
}
.login-input-container {
    border: 1px solid white;
    margin-bottom: 10px;
    border-radius: 10px;
}
.login-input-container input{
    padding:0;
    height:18px!important;
}
.login-label {
    margin-top:0;
    margin-left: 10px!important;
    font-size: 13px;
    font-weight: bold;
}

.login-container ul li p {
    color:white;
    margin-top:5px;
    margin-bottom:0px;
    text-align:left;
}
.login-container ul li input {
    border-radius:10px;
    height:35px;
    width:100%;
    outline:none!important;
    border:none;
    background-color: #181818;
    padding-left:5px;
    color:white;

}
.login-title {
    color:white;
    font-size:25px;
    margin-bottom: 0px;
}
.login-container ul li {
    list-style-type: none;
}
.login-container ul {
    padding-left: 0;
}
.login-container {
    
    text-align:center!important;
    margin-top:60px;
    min-height: 100vh;
} 
.login-container-child {
    
    margin-top:20px;

}
.forgot-pw {
    color: #d4d4d4;
    text-align: center!important;
}
.create-account-link{
    margin-top: 20px!important;
    font-weight: bold;
}
/* desktop styles */
@media only screen and (min-width: 725px) {
    .login-container {
        min-width: 400px;
        width:40%;
        margin-left: auto;
        margin-right:auto;
        text-align: center;
    }
    .login-home-btn {
        padding: 3px 5px!important;
        font-size: 15px;
        font-weight: bold;

    }
  }
/* mobile styles */
@media only screen and (max-width: 725px) {
    .login-container {
        min-width: 350px;
        width:70%;
        margin-left: auto!important;
        margin-right:auto!important;
        text-align: left;
    }
    .login-home-btn {
        padding: 3px 5px!important;
        font-size: 15px;
        font-weight: bold;

    }
    .login-text {
        font-size: 13px;
    }
  }